import React, { useEffect } from 'react';

function RandomQuote({ setQuote }) {
  useEffect(() => {
    const quotes = [
        "You are more than what you think you are.",
        "Youve already overcome so much  you can handle this too.",
        "You are allowed to be both a masterpiece and a work in progress.",
        "You dont need permission to believe in yourself.",
        "You carry strength you haven't even discovered yet.",
        "You are not defined by your doubts  you are defined by your actions.",
        "Youre not behind; youre just building something worth being proud of.",
        "Youre capable of far more than what you give yourself credit for.",
        "You have what it takes, even on the days you dont feel it.",
        "You were never meant to fit in  youre meant to stand out.",
        "You are becoming exactly who youre meant to be.",
        "You don't have to be perfect to be powerful.",
        "Youve done brave things before. You can do this too.",
        "You dont just have potential  you have purpose.",
        "You were made for more than playing small."
    ];

    const randomIndex = Math.floor(Math.random() * quotes.length);
    setQuote(quotes[randomIndex]);
  }, [setQuote]);

  return null; 
}

export default RandomQuote;
